"use client";
import { useState, useEffect } from "react";
import Image from "next/image";
import { BiCalendar, BiEdit, BiTrash } from "react-icons/bi";
import { eventService } from "@/services/eventService";
import toast from "react-hot-toast";

export default function OurEventsTable() {
  const [events, setEvents] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    fetchEvents();
  }, []);

  const fetchEvents = async () => {
    setIsLoading(true);
    try {
      console.log("Fetching events...");
      // Fetch all events (no status filter for admin - show all)
      const response = await eventService.getEvents({
        page: 1,
        limit: 100, // Get all events
        showAll: true // Tell backend to show all events regardless of status
      });
      
      console.log("Events response:", response);
      
      // Backend returns: { status: 'success', data: [...], pagination: {...} }
      const eventsData = response?.data || response?.events || [];
      console.log("Events loaded:", eventsData.length);
      console.log("Sample event:", eventsData[0]);
      
      setEvents(Array.isArray(eventsData) ? eventsData : []);
    } catch (error) {
      console.error('Failed to load events:', error);
      console.error('Error details:', error.response?.data || error);
      toast.error(error.response?.data?.message || 'Failed to load events');
      setEvents([]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = (eventId) => {
    // Navigate to edit page
    window.location.href = `/admin/events/${eventId}/edit`;
  };

  const handleDelete = async (eventId) => {
    if (!confirm("Are you sure you want to delete this event?")) {
      return;
    }
    
    try {
      await eventService.deleteEvent(eventId);
      toast.success("Event deleted successfully");
      fetchEvents(); // Refresh the list
    } catch (error) {
      console.error("Failed to delete event:", error);
      toast.error(error.response?.data?.message || "Failed to delete event");
    }
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Our Events</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">All Events kro</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border p-6">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-semibold text-blue-900">Our Events</h2>
          <div className="flex items-center gap-4">
            <button
              onClick={fetchEvents}
              className="px-4 py-2 bg-blue-600 text-white text-sm rounded-lg hover:bg-blue-700 transition-colors"
            >
              Refresh
            </button>
            <div className="flex items-center gap-2">
              <select 
                value={entriesPerPage} 
                onChange={(e) => {
                  setEntriesPerPage(Number(e.target.value));
                  setCurrentPage(1);
                }} 
                className="border rounded-lg px-3 py-2 text-sm"
              >
                <option value={10}>10</option>
                <option value={25}>25</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </select>
              <span className="text-sm text-orange-600 font-medium">entries per page</span>
            </div>
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="bg-gray-50 border-b">
                <th className="px-4 py-3 text-left text-sm font-semibold text-orange-600">Event Name</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-orange-600">Short Description</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-orange-600">Event Date</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-orange-600">Event Img</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-orange-600">Status</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-orange-600">Action</th>
              </tr>
            </thead>
            <tbody>
              {isLoading ? (
                <tr>
                  <td colSpan="6" className="px-4 py-12 text-center text-gray-500">
                    <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-orange-500 mx-auto mb-2"></div>
                    Loading events...
                  </td>
                </tr>
              ) : events.length > 0 ? (
                events
                  .slice((currentPage - 1) * entriesPerPage, currentPage * entriesPerPage)
                  .map((event, index) => (
                    <tr key={event._id || event.id} className={`border-b hover:bg-gray-50 transition-colors ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'}`}>
                      <td className="px-4 py-3 text-sm font-medium text-gray-900">{event.heading || event.name || 'N/A'}</td>
                      <td className="px-4 py-3 text-sm text-gray-700 max-w-md">
                        <div className="line-clamp-2">{event.shortBrief || event.shortDescription || 'N/A'}</div>
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {event.date ? new Date(event.date).toLocaleDateString('en-GB', {
                          day: '2-digit',
                          month: '2-digit',
                          year: 'numeric'
                        }) : 'N/A'}
                      </td>
                      <td className="px-4 py-3">
                        {event.picture?.url || event.image ? (
                          <div className="w-20 h-20 relative rounded border border-gray-200 overflow-hidden">
                            <Image 
                              src={event.picture?.url || event.image} 
                              alt={event.heading || event.name || 'Event'} 
                              fill 
                              className="object-cover"
                            />
                          </div>
                        ) : (
                          <div className="w-20 h-20 relative rounded border border-gray-200 bg-gray-100 flex items-center justify-center">
                            <span className="text-gray-400 text-2xl">ðŸ“·</span>
                          </div>
                        )}
                      </td>
                      <td className="px-4 py-3">
                        <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                          event.status === 'published' || event.status === 'active' 
                            ? 'bg-green-100 text-green-700' 
                            : event.status === 'draft'
                            ? 'bg-yellow-100 text-yellow-700'
                            : 'bg-gray-100 text-gray-700'
                        }`}>
                          {event.status || 'N/A'}
                        </span>
                      </td>
                      <td className="px-4 py-3">
                        <div className="flex flex-col gap-2">
                          <button 
                            onClick={() => handleEdit(event._id || event.id)}
                            className="px-4 py-2 bg-blue-600 text-white text-sm rounded-lg hover:bg-blue-700 transition-colors"
                          >
                            Edit
                          </button>
                          <button 
                            onClick={() => handleDelete(event._id || event.id)}
                            className="px-4 py-2 bg-red-600 text-white text-sm rounded-lg hover:bg-red-700 transition-colors"
                          >
                            Delete
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
              ) : (
                <tr>
                  <td colSpan="6" className="px-4 py-12 text-center text-gray-500">
                    <div className="text-4xl mb-2">ðŸ“…</div>
                    <p className="text-lg font-medium">No events found</p>
                    <p className="text-sm text-gray-400 mt-2">
                      Create your first event to get started.
                    </p>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        {events.length > 0 && (
          <div className="flex items-center justify-between mt-6">
            <div className="text-sm text-gray-600">
              Showing {((currentPage - 1) * entriesPerPage) + 1} to {Math.min(currentPage * entriesPerPage, events.length)} of {events.length} entries
            </div>
            <div className="flex items-center gap-2">
              {Array.from({ length: Math.ceil(events.length / entriesPerPage) }, (_, i) => i + 1).map((page) => (
                <button
                  key={page}
                  onClick={() => setCurrentPage(page)}
                  className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                    currentPage === page
                      ? 'bg-orange-200 text-orange-900'
                      : 'text-gray-600 hover:bg-gray-100'
                  }`}
                >
                  {page}
                </button>
              ))}
            </div>
          </div>
        )}
      </div>
    </>
  );
}



